/* ==========================================================================
   #RESET
   ========================================================================== */
/**
 * A very simple reset that sits on top of Normalize.css.
 */
body,
h1, h2, h3, h4, h5, h6,
blockquote, p, pre,
dl, dd, ol, ul,
figure,
hr,
fieldset, legend {
  margin: 0;
  padding: 0; }

/**
  * Remove trailing margins from nested lists.
  */
li > ol,
li > ul {
  margin-bottom: 0; }

/**
  * Remove default table spacing.
  */
table {
  border-collapse: collapse;
  border-spacing: 0; }

/**
  * 1. Reset Chrome and Firefox behaviour which sets a `min-width: min-content;`
  *    on fieldsets.
  */
fieldset {
  min-width: 0;
  /* [1] */
  border: 0; }

/* ELEMENTS */
/* ==========================================================================
   #BODY
   ========================================================================== */
/**
 * Body section element settings.
 */
body {
  background-color: #F0F2F5;
  font-family: 'Poppins', sans-serif; }

/*============================================================================
    #HEADINGS
============================================================================= */
/* Basic styles of headings. */
h1 {
  font-size: 2.25rem;
  font-weight: 100; }

h2 {
  margin-top: 3.8rem;
  margin-bottom: 0.8rem;
  color: #363F5F;
  font-size: 1.5rem;
  font-weight: 400; }

h3 {
  font-size: 1.3125rem;
  font-weight: 400; }

h4 {
  font-size: 1.125rem; }

h5 {
  font-size: 1rem; }

h6 {
  font-size: 0.875rem; }

/*============================================================================
    #PARAGRAPH
============================================================================= */
/* Basic styles of . */
p {
  font-size: 2rem;
  line-height: 3rem;
  margin-top: 1rem; }

/*============================================================================
    #TABLE
============================================================================= */
/* Basic table styles. */
table {
  border-spacing: 0; }

/* LAYOUT */
/*============================================================================
    #HEADER
============================================================================= */
/*  Main page header */
.l-header {
  background-color: #2D4A22;
  padding-top: 2rem;
  padding-bottom: 10rem;
  padding-right: 0;
  padding-left: 0;
  text-align: center; }

/*============================================================================
    #FOOTER
============================================================================= */
/* Footer styles. */
.l-footer {
  text-align: center; }

/*============================================================================
    #BALANCE
============================================================================= */
/* Balance section styles */
.l-balance {
  margin-top: -8rem; }

/*============================================================================
    #CARDS-LAYOUT
============================================================================= */
/* Cards styles. */
.l-balance .c-card {
  margin-bottom: 2rem;
  padding: 1.5rem 2rem; }

/*============================================================================
    #CONTAINER
============================================================================= */
/* Container styles. */
.c-container {
  margin: auto; }

/* COMPONENTS */
/*============================================================================
    #HEADINGS-COMPONENTS
============================================================================= */
/* Advanced headings styles. */
/* H1 */
.c-header__logo {
  color: #FFFFFF; }

/* H2 */
.c-balance__title {
  color: #FFFFFF;
  margin-top: 0; }

/* H3 */
.c-card__title {
  font-size: 1rem; }

/*============================================================================
    #CARDS
============================================================================= */
/* Cards styles. */
.c-card {
  background-color: #FFFFFF;
  border-radius: 0.25rem;
  color: #363F5F; }

/* =============================================================================
  Color variants
 =============================================================================*/
.c-card--total {
  background-color: #49AA26;
  color: #FFFFFF; }

/*============================================================================
    #TABLE-COMPONENTS
============================================================================= */
/* Advanced table styles. */
.c-data-table {
  width: 100%; }
